export default "import React from 'react';\nimport useEmblaCarousel from 'embla-carousel-react';\nimport { NextButton, PrevButton, usePrevNextButtons } from '../../EmblaCarouselArrowButtons';\nimport GapSizeForm from './EmblaCarouselGapSizeForm';\nimport SlideSizeForm from './EmblaCarouselSlideSizeForm';\nimport { SelectedSnapDisplay, useSelectedSnapDisplay } from '../../EmblaCarouselSelectedSnapDisplay';\nimport { sandboxImages } from 'components/Sandbox/sandboxImages';\n\nconst EmblaCarousel = (props) => {\n    const { slides, options } = props;\n    const [emblaRef, emblaApi] = useEmblaCarousel(options);\n    \n    const { prevBtnDisabled, nextBtnDisabled, onPrevButtonClick, onNextButtonClick } = usePrevNextButtons(emblaApi);\n    \n    const { selectedSnap, snapCount } = useSelectedSnapDisplay(emblaApi);\n    \n    return (<div className=\"embla\">\n      <SlideSizeForm emblaApi={emblaApi} property=\"--slide-size\" initialValue=\"100%\"/>\n \n      <GapSizeForm emblaApi={emblaApi} property=\"--slide-gap\" min={0} max={50} initialValue={0} unit=\"px\"/>\n \n      <div className=\"embla__viewport\" ref={emblaRef}>\n        <div className=\"embla__container\">\n          {slides.map((index) => (<div className=\"embla__slide\" key={index}>\n              <img className=\"embla__slide__img\" src={sandboxImages(index)} alt=\"Your alt text\"/>\n            </div>))}\n        </div>\n      </div>\n \n      <div className=\"embla__controls\">\n        <div className=\"embla__buttons\">\n          <PrevButton onClick={onPrevButtonClick} disabled={prevBtnDisabled}/>\n          <NextButton onClick={onNextButtonClick} disabled={nextBtnDisabled}/>\n        </div>\n \n        <SelectedSnapDisplay selectedSnap={selectedSnap} snapCount={snapCount}/>\n      </div>\n    </div>);\n};\n\nexport default EmblaCarousel;\n";